/*------------------------------ <FOOTER> ------------------------------*/
footer {

  .footer {

    &-body {
      @include flexbox();
      height: 125px;
      justify-content: space-between;
      align-items: center;

      @media (max-width: $md2+px) {
        height: auto;
        padding: 15px 0;
        flex-direction: column;
      }

      &__column {

        @media (max-width: $md2+px) {

          &:first-child {
            @include flexorder(2);
          }

          &:last-child {
            @include flexorder(1);
            margin: 0 0 15px 0;
          }
        }
      }
    }

    &__text {
      color: #4d4d4d;
      font-size: 15px;
      line-height: 25px;

      @media (max-width: $md2+px) {
        text-align: center;
        font-size: 14px;
      }

      a {
        text-decoration: underline;
        color: #4d4d4d;

        &:hover {
          text-decoration: none;
        }
      }
    }

    &__logo {}
  }
}
/*------------------------------ </FOOTER> ------------------------------*/